
import Datetime from 'vux/src/components/datetime/index.vue'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'

import Vue from 'vue'
Vue.use(DatetimePlugin)
export default {
data() {
return {
show: false,
startDate: '',
endDate: '',
visibility: false,
visibility2: false,
widths: 100,
tableHead: ["订单编号", "订单日期", "物料名称物料名称", "币别", "金额"]
};
},
components: {
Datetime,
},
mounted() {
this.widths = 6 * this.widths + "px";
console.log($(window).width());
},
methods: {
showPlugin () {
this.$vux.datetime.show({
cancelText: '取消',
confirmText: '确定',
format: 'YYYY-MM-DD HH',
value: '2017-05-20 18',
onConfirm (val) {
console.log('plugin confirm', val)
},
onShow () {
console.log('plugin show')
},
onHide () {
console.log('plugin hide')
}
})
},
onConfirm() {

},
log() {

},
popup() {
alert('0')
},
change() {

}
}
};



